@import '../../styles/variables.scss';

@keyframes emoji-spinner {
    0% {
        
    }
    50% {
        transform: rotateZ(360deg);
    }
    to {
        transform: rotateZ(0deg);
    }
}

#emoji-char {
    animation: 4s ease-in-out 0s infinite emoji-spinner;
    margin: 0 0.3rem 0 0.3rem;
    border-radius: 50%;
}

.header-main {
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
    margin-bottom: 1rem;
    color: $main-color;
    font-size: 2rem;

    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */

    b {
        font-style: italic;
        font-size: 42px;
        font-weight: 600;
    }

    .header-line {
        margin-top: 0.5rem;
        background-color: $second-color;
        width: 90%;
        height: 0.2rem;
    }
}

@media only screen and (min-width: 700px) {
    // computer screens
    .header-main {
        margin-left: 5rem;

        a {
            color: $main-color;
            transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
            box-shadow: inset 0 0 0 0 #54b3d6;
            margin: 0 .5rem;
            padding: 0 .5rem;
            border-radius: 10px;

            &:hover {
                color: white;
                box-shadow: inset 150px 0 0 0 #54b3d6;
            }
        }
    }
};

@media only screen and (max-width: 701px) {
    // phone screens
    .header-main {
        margin-left: 1.5rem;

        a {
            margin: 0 .5rem;
        }
    }
};